* {
    padding:0;
    margin:0;
}

#Contact {
    font-size: 5.2em;
    margin-bottom: 20px;
    color: black;
    text-align: center;
    @media screen and (max-width: 900px) {
        font-size: 3em;
        padding-bottom: 1%;
    }
}

.email {
    font-size: 5em;
    margin-bottom: 20px;
    color: black;
    text-align: center;
    @media screen and (max-width: 900px) {
        font-size: 1.5em;
        font-weight: 600;
    }
}



.row-contact {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap:100px;
    gap: 20px;
}

.icon-columns {
    display: flex;
    flex-direction: column-reverse;
}

.links-name {
    border-radius: 90%;
    &:hover {
        box-shadow: 0 0 25px rgb(179, 65, 65);
    }
}

.hide {
    display:none;
}

.links-name:hover + .hide {
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    font-style: italic;
    font-size: 1.5em;
}